<script>
export default {
  data() {
    return {
      answers: [],
      dimensions: {
        width: "40px",
        height: "30px",
        border: "1px solid #e27217"
      },
      hoveredColor: "",
      hoveredIndex: "",
      selectedColors: [0, 0, 0, 0, 0, 0, 0],
      colores: ["rojo", "azul", "amarillo", "gris", "negro", "verde", "blanco"]
    };
  },
  computed: {},
  methods: {
    resetIndex() {
      this.hoveredColor = "";
      this.hoveredIndex = "";
    },
    checkIfColored(color, index) {
      return {
        rojo:
          color === "rojo" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[0] >= index),
        azul:
          color === "azul" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[1] >= index),
        amarillo:
          color === "amarillo" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[2] >= index),
        gris:
          color === "gris" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[3] >= index),
        negro:
          color === "negro" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[4] >= index),
        verde:
          color === "verde" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[5] >= index),
        blanco:
          color === "blanco" &&
          ((this.hoveredColor === color && this.hoveredIndex >= index) ||
            this.selectedColors[6] >= index)
      };
    },
    checkIfColor(color, index) {
      this.hoveredColor = color;
      this.hoveredIndex = index;
    },
    colorChecked(color, index) {
      switch (color) {
        case "rojo": {
          this.selectedColors[0] = index;
          return;
        }
        case "azul": {
          this.selectedColors[1] = index;
          return;
        }
        case "amarillo": {
          this.selectedColors[2] = index;
          return;
        }
        case "gris": {
          this.selectedColors[3] = index;
          return;
        }
        case "negro": {
          this.selectedColors[4] = index;
          return;
        }
        case "verde": {
          this.selectedColors[5] = index;
          return;
        }
        case "blanco": {
          this.selectedColors[6] = index;
          return;
        }
      }
    }
  }
};
</script>

